@charset "uft-8";

/* 초기화 작업 */
html, body, header, div, nav, footer, ul, li, dl, dt, dd, h2, p {
    margin: 0;
    padding: 0;
}

ul {
    list-style: none;
}

/* 링크 스타일 */
a:link {
    color: #000;
    text-decoration: none;
}
a:visited {
    color: #000;
}

/* 웹 폰트 */
@import url("https://fonts.googleapis.com/css2?family=Black+Han+Sans&family=Noto+Sans+KR:wght@300;400;500;700&family=Roboto:wght@400;500&display=swap");
.App {
    font-family: 'Noto Sans KR', sans-serif;
}

/* 기본 스타일 */
* {
    box-sizing: border-box;
}

body {
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.8;
    color: #212529;
    text-align: left;
    background-color: #fff;
}

div {
    display: block;
}

dd {
    display: block;
}

.navbar {
    display: flex;
    flex-wrap: wrap;
    flex-flow: row nowrap;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    padding: 0.5rem 1rem;
    box-shadow: 0 3px 6px rgb(0 0 0 / 16%);
    align-items: center;
    justify-content: flex-start;
    background-color: #fff;
    z-index: 1000;
}

.logo {
    padding: 0.5rem;
}

header #logo {
    width: 165px;
    vertical-align: middle;
}

nav {
    display: flex;
    flex-basis: auto;
    flex-direction: row;
    margin-left: 1rem;
    margin-right: 1rem;
    justify-content: flex-end!important;
    flex-grow: 1;
    align-items: center;
    font-weight: 500;
    text-align: left;
}
nav .nav_btn {
    font-size: 20px;
    padding: 0 0.7rem;
}

.login {
    display: inline;
    border: 1px solid;
    border-color: #4e4e4e;
    border-radius: 2rem;
    min-width: 90px;
    padding: 0.25rem 0.5rem;
    margin-left: auto;
    margin-right: 0.5rem;
    text-align: center;
    vertical-align: middle;
}

.main_image img {
    margin: 52px auto 0;
    height: inherit;
    object-fit: cover;
    width: 100%;
}

.contents {
    width: 100%;
    padding-top: 3rem!important;
    padding: 10px 15px 64px 15px;
    margin-right: auto;
    margin-left: auto;
    max-width: 1030px;
    margin-bottom: auto;
}

.contents .no_mb {
    margin: 50px auto 0;
}
.contents_inner h2 {
    margin: 50px auto;
    font-weight: 700;
    text-align: center;
    font-size: 2rem;
}

.contents_inner {
    padding-bottom: 3rem;
    margin-bottom: 3rem;
    margin-top: 3rem;
}

.contents_inner .post_body {
    padding: 32px 0;
    border-bottom: 1px solid #ececec;
}
.post_body a {
    font-size: 16px;
    line-height: 1.5;
    margin-top: 0;
    margin-bottom: 0.5rem;
    color: #5F6368;
}
.post_body time {
    display: block;
    padding-top: 0.5rem;
    color: #9E9E9E;
}

.contents .more {
    margin-top: 32px;
    margin-bottom: 3rem;
    font-weight: 500;
    text-align: center;
    font-size: 14px;
    line-height: 1.8;
    color: #000;
}
.more a {
    display: block;
    position: relative;
    width: 90%;
    max-width: 285px;
    padding: 0.8em;
    margin: 0 auto;
    border: 1px solid #4e4e4e;
    border-radius: 50px;
}

.dl_profile {
    display: inline-block;
    position: relative;
    width: 20%;
    max-width: 33.333333%;
    flex: 0 0 20%;
    padding-right: 20px;
    padding-left: 20px;
}
#profile dt img {
    width: 100%;
    vertical-align: middle;
    border-radius: 50%;
    border: 1.5px solid #4e4e4e;
}
#profile dd {
    text-align: center;
    margin-bottom: 3rem;
    font-size: 1.3em;
    font-weight: 700;
    margin-top: 1rem;
}

.contents_inner.release {
    border: 1px solid #4e4e4e;
    margin-bottom: 12em;
}
.inbox {
    padding: 0 150px;
}
.inbox img {
    width: 100%;
    height: auto;
}

.r_release {
    font-size: 24px;
    font-weight: 700;
    text-align: center;
}
.r_title {
    font-size: 40px;
    margin-bottom: 30px;
    font-weight: 700;
    text-align: center;
}
.r_day {
    font-size: 25px;
    color: #a5a5a5;
    font-weight: 700;
    text-align: center;
}

.iframe {
    position: relative;
    height: 0;
    padding-bottom: 56.25%;
    overflow: hidden;
}
.iframe iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.sns_group {
    display: flex;
    justify-content: center;
    flex-wrap: nowrap;
}
.sns_group li {
    width: 50px;
    margin: 30px 20px;
}
.sns_group img {
    width: 100%;
    vertical-align: middle;
}

footer {
    padding: 32px 0;
    border-top: 1px solid #c9c9c9;
}
address {
    max-width: 1030px;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    font-style: normal;
}
address .txt {
    font-size: 12px;
}